'use client';

import React, { useMemo, useState } from 'react';
import { 
  BookOpen, Globe, ArrowRight, Search, Hash, 
  Info, ShieldCheck, Database, Landmark, Scale, Star, MousePointerClick 
} from 'lucide-react';
// Import data JSON tunggal agar sinkron
import treatyDataRaw from '@/data/treaties.json';
import Link from 'next/link';

interface Treaty {
  pair: string;
}

export default function EncyclopediaPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const treatyData = (treatyDataRaw as unknown) as Treaty[];

  /**
   * HELPER: createSlug
   * Ensures 100% URL consistency to prevent 404 errors.
   */
  const createSlug = (pair: string) => {
    if (!pair || typeof pair !== 'string') return "";
    const parts = pair.split(/[-–—]/); 
    if (parts.length < 2) return "";
    const source = parts[0].trim().toLowerCase().replace(/\s+/g, '-');
    const target = parts[1].trim().toLowerCase().replace(/\s+/g, '-');
    return `/tax-treaty/tax-treaty-${source}-to-${target}`;
  };

  /**
   * SMART SEARCH ENGINE
   * Normalizes dashes and spaces for a "fuzzy" search experience.
   */
  const groupedTreaties = useMemo(() => {
    const normalizedSearch = searchTerm.toLowerCase().replace(/[-–—]/g, ' ').trim();
    const searchParts = normalizedSearch.split(/\s+/);

    const filtered = treatyData.filter(t => {
      if (!t || !t.pair || typeof t.pair !== 'string') return false;
      const normalizedPair = t.pair.toLowerCase().replace(/[-–—]/g, ' ');
      return searchParts.every(part => normalizedPair.includes(part));
    });

    const groups: { [key: string]: Treaty[] } = {};
    filtered.forEach(t => {
      const firstLetter = t.pair.trim()[0]?.toUpperCase() || '#';
      if (!groups[firstLetter]) groups[firstLetter] = [];
      groups[firstLetter].push(t);
    });

    return Object.keys(groups).sort().reduce((acc, key) => {
      acc[key] = groups[key];
      return acc;
    }, {} as { [key: string]: Treaty[] });
  }, [searchTerm, treatyData]);

  // Data contoh untuk "Quick Access" agar user tidak bingung
  const popularCorridors = [
    { name: "United States - Indonesia", slug: "tax-treaty-united-states-to-indonesia" },
    { name: "United Kingdom - Japan", slug: "tax-treaty-united-kingdom-to-japan" },
    { name: "Canada - Singapore", slug: "tax-treaty-canada-to-singapore" },
    { name: "Australia - Germany", slug: "tax-treaty-australia-to-germany" }
  ];

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      
      {/* 1. HERO & SEARCH SECTION: Clear Guidance */}
      <section className="max-w-6xl mx-auto pt-24 pb-16 px-8 text-center space-y-8">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <BookOpen className="w-4 h-4" /> Global Registry Hub
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          Tax Treaty <span className="text-[#D4AF37]">Encyclopedia.</span>
        </h1>
        <p className="max-w-2xl mx-auto text-gray-400 font-light italic text-[18px] leading-relaxed">
          Access 10,000+ jurisdictional tax corridors. Search by country name to find specific 2026 withholding tax (WHT) data.
        </p>
        
        {/* Improved Search Bar with Examples */}
        <div className="max-w-2xl mx-auto relative group">
          <Search className="absolute left-6 top-1/2 -translate-y-1/2 text-gray-300 group-focus-within:text-[#D4AF37] transition-colors" size={22} />
          <input 
            type="text"
            placeholder="Try 'USA', 'United Kingdom', or 'Indonesia'..."
            className="w-full bg-white border border-gray-100 py-7 pl-16 pr-8 rounded-[2.5rem] shadow-2xl shadow-gray-100/50 focus:ring-2 focus:ring-[#D4AF37] outline-none transition-all text-[17px] italic font-light"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        {/* 2. QUICK ACCESS SECTION: One-click for confused users */}
        <div className="max-w-3xl mx-auto pt-8">
           <p className="text-[11px] font-black uppercase tracking-widest text-gray-300 mb-6 flex items-center justify-center gap-2">
             <Star size={12} className="text-[#D4AF37]" /> Popular Jurisdictions
           </p>
           <div className="flex flex-wrap justify-center gap-3">
              {popularCorridors.map((c, i) => (
                <Link key={i} href={`/tax-treaty/${c.slug}`} className="px-5 py-2.5 bg-white border border-gray-100 rounded-full text-[13px] font-medium text-gray-500 hover:border-[#D4AF37] hover:text-black transition-all shadow-sm">
                  {c.name}
                </Link>
              ))}
           </div>
        </div>
      </section>

      {/* 3. USER GUIDELINES: Explain the Page Value (SEO Content) */}
      <section className="max-w-5xl mx-auto px-8 mb-32 grid md:grid-cols-3 gap-8">
        <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-4 shadow-sm">
          <MousePointerClick className="text-[#D4AF37]" size={28} />
          <h3 className="text-xl font-serif italic">1. Select Pair</h3>
          <p className="text-sm text-gray-400 leading-relaxed font-light">
            Search or browse our directory to select a bilateral tax corridor (Source to Target country).
          </p>
        </div>
        <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-4 shadow-sm">
          <Database className="text-[#D4AF37]" size={28} />
          <h3 className="text-xl font-serif italic">2. View Rates</h3>
          <p className="text-sm text-gray-400 leading-relaxed font-light">
            Get instant access to 2026 withholding tax rates for Dividends, Interest, and Royalties.
          </p>
        </div>
        <div className="p-10 bg-white border border-gray-50 rounded-[3rem] space-y-4 shadow-sm">
          <ShieldCheck className="text-[#D4AF37]" size={28} />
          <h3 className="text-xl font-serif italic">3. Compliance</h3>
          <p className="text-sm text-gray-400 leading-relaxed font-light">
            Review Article-specific compliance roadmaps including Residency and Beneficial Ownership rules.
          </p>
        </div>
      </section>

      {/* 4. DYNAMIC ALPHABETICAL INDEX */}
      <section className="max-w-7xl mx-auto px-8 space-y-24">
        {Object.entries(groupedTreaties).map(([letter, items]) => (
          <div key={letter} className="space-y-10">
            <div className="flex items-center gap-8">
              <div className="w-20 h-20 bg-black text-[#D4AF37] rounded-[2rem] flex items-center justify-center text-4xl font-serif italic shadow-2xl">
                {letter}
              </div>
              <div className="h-[1px] flex-1 bg-gray-100"></div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-5">
              {items.map((item, idx) => (
                <Link 
                  key={idx} 
                  href={createSlug(item.pair)}
                  className="flex items-center justify-between p-8 bg-white border border-gray-50 rounded-[2.5rem] hover:border-[#D4AF37] hover:shadow-2xl transition-all group"
                >
                  <div className="flex items-center gap-6">
                    <Globe size={22} className="text-gray-200 group-hover:text-[#D4AF37] transition-colors" />
                    <span className="text-[16px] font-medium text-gray-600 group-hover:text-black transition-colors font-serif italic">
                      {item.pair.replace('-', ' → ')}
                    </span>
                  </div>
                  <ArrowRight size={16} className="text-gray-200 group-hover:text-black group-hover:translate-x-2 transition-all" />
                </Link>
              ))}
            </div>
          </div>
        ))}
      </section>

      {/* 5. LONG-FORM SEO CONTENT (AdSense Approval) */}
      <section className="max-w-4xl mx-auto mt-40 px-8 py-24 bg-black rounded-[5rem] text-center space-y-8 shadow-2xl">
        <Hash className="mx-auto text-[#D4AF37] opacity-30" size={64} />
        <h3 className="text-3xl font-serif italic text-white">2026 Global Jurisdictional Archive</h3>
        <p className="text-gray-400 font-light leading-relaxed italic text-[16px]">
          GlobalTax Laboratory provides this encyclopedia as a professional utility for cross-border tax analysis. 
          By indexing 10,000+ bilateral corridors, we empower multinational architects with 2026 fiscal intelligence. 
          Each entry is synchronized with current OECD Double Taxation Avoidance Agreement (DTAA) protocols.
        </p>
      </section>
    </div>
  );
}